import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import { SERVERURL } from '../App';

export default function Login(props) {
    const navigate = useNavigate();

    const [username, setUsername] = useState("");
    const [password, setPassword] = useState("");
    
    const submitLogin = () => {
        console.log(username !== "" && password !== "");
        console.log(SERVERURL + 'login')
        if(username !== "" && password !== ""){
            axios.post(SERVERURL + 'login', {
                username: username,
                password: password
            }).then(response => {
                console.log(response);
                if(!response.data.auth){
                    alert(response?.data.message);
                } else {
                    localStorage.setItem("token", response.data.token);
                    navigate('/');
                }
            })
        }
    }
    
    return(
        <div className="bg-dark bg-opacity-50 position-fixed top-0 bottom-0 start-0 end-0 d-flex flex-column justify-content-center align-items-center">
            <div className=" bg-light  bg-opacity-10 p-5 rounded">
                <h1 className="mb-3 text-white">Login</h1>
                <form 
                style={{
                    color: 'white',
                    minWidth: '20em'
                }}>
                    <div class="mb-3">
                        <label for="InputUser" class="form-label">Username</label>
                        <input type="text" class="form-control" id="InputUser" name="username" onChange={e => setUsername(e.target.value)}/>
                    </div>
                    <div class="mb-4">
                        <label for="inputPass" class="form-label">Password</label>
                        <input type="password" class="form-control" id="inputPass" name="password" onChange={e => setPassword(e.target.value)}/>
                    </div>
                    <button type="button" class="btn btn-primary align-self-end" onClick={() => submitLogin()}>Submit</button>
                </form>
            </div>
            
        </div>
    )

}